 Design had its humble roots in typography and architecture and has branched out to an ever-rising number of fields and areas of application, but has always had the same motives; solve problems, address needs, humanize. Initially, design was used in methods of preserving and transmitting knowledge in the forms of manuscripts and encyclopedias, evolving with the changing techniques and technology of printing and type setting. With the onset of the industrial revolution, new jobs such as advertising, book publishing and art direction, allowed designers new ways to broadcast information of otherwise machine-made products. The industrial revolution also brought about industrial design as a means of addressing a variety of human problems with speed and mass production. With the advancement of technology, design as a discipline widened, fields of Interaction, Ergonomics, Service (to name a few) came about to address a variety of different and evolving user needs. ")]),_c('h3',{staticClass:"h3-upper-case",staticStyle:{"margin-bottom":"10%"}},[_vm._v("Or did it?")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{attrs:{"id":"today"}},[_c('h1',{staticClass:"h1-upper-case"},[_vm._v(" now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now now ")]),_c('h2',{staticClass:"h1-upper-case"},[_vm._v("now")]),_c('p',[_vm._v(" Today, design is used everywhere, from designing more humane prisons, to making space travel safer for astronauts. On the other side however, is where design is seen as a box to just check off, a way to bring in more revenue, provide a competitive advantage or make something more addictive. With job descriptions